.container {
    max-width: 1170px;
    margin: auto;
}
.row {
    display: flex;
}
ul {
    list-style: none;
}

.footer {
    background-color: #0b1f60e9;
    padding: 70px 0;
}
.footer-col {
    width: 25%;
    padding: 0 15px;
}
.footer-col h4 {
    font-size: 18px;
    color: #fff;
    text-transform: capitalize;
    margin-bottom: 30px;
    position: relative;
}
.footer-col h4::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    background-color: #e91e63;
    height: 2px;
    box-sizing: border-box;
    width: 50px;
}
 li {
    font-size: 17px;
    text-transform: capitalize;
    text-decoration: none;
    color: #ffff;
    font-weight: 500;
    display: block;
    transform: all 0.3s ease;
}
li :hover {
    color: #ffff;
    padding-left: 10px;
}
.footer-col .social-links li {
    display: inline-block;
    height: 40px;
    width: 40px;
    background-color: black;
    margin: 0 10px 10px 0;
    text-align: center;
    line-height: 40px;
    border-radius: 50px;
    color: #fff;
    transform: all 0.3s ease;
}
.footer-col .social-links  :hover {
    color: #24262b;
    background-color: #ffff;
}